<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body row p-0">
        <div class="col-sm-12">
          <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
        </div>
        <div class="col-sm-12">
          <my-table :columnDefs="columnDefs" v-bind:data-rows="histories" :actions="actions" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import historySchemas from './history-schemas'
  import dtHelper from 'src/helpers/datatable'

  export default{
    components: {
      MyTable,
      MyFilter
    },
    computed: {
      histories() {
        let self = this;
        let rows = this.$store.state.histories;
        if (rows.length) {
          rows.forEach(function (item) {
            item.actionText = self.parseAction(item.action) + ' ' + self.parseType(item.type) + ' ' + item.type_title
          })
        }
        return dtHelper.filterByRules(rows, this.filterOutput);
      }
    },
    data () {
      return {
        filterOutput: [],
        columnDefs: dtHelper.buildColumDefs(historySchemas),
        actions: [],
        filter: {},
        rules: dtHelper.buildRules(historySchemas)
      }
    },
    mounted() {
      window.AP = this;
      this.$store. dispatch('fetchHistories');

      this.$store.dispatch('setPageTitle', 'lch s hot ng');
    },
    methods: {
      parseAction(action) {
        switch(action) {
          case 'create': return 'to';
          case 'restore': return 'hi phc';
          case 'update': return 'cp nht';
          case 'delete': return 'xa';
          case 'inactive': return 'n';
          case 'active': return 'hin';
          case 'logout': return 'ng xut';
          case 'login': return 'ng nhp';
          default: return action;
        }
        return action;
      },
      parseType(type) {
        switch(type) {
          case 'menu': return 'menu';
          case 'attribute': return 'thuc';
          case 'setting': return 'thit lp chung';
          case 'product': return 'sn phm';
          case 'sale': return 'chng trnh gim gi';
          case 'collection': return 'nhm sn phm';
          case 'blog': return 'nhm bi vit';
          case 'article': return 'bi vit';
          case 'page': return 'trang ni dung';
          case 'coupon': return 'm gim gi';
          case 'role': return 'quyn';
          case 'client': return 'khch hng tiu biu';
          case 'testimonial': return 'i tc ni v chng ti';
          case 'order': return 'n hng';
          case 'contact': return 'lin h';
          case 'gallery': return 'th vin nh';
          default: return '';
        }
        return '';
      },
      updateFilter(filterOutput) {
        this.filterOutput = filterOutput;
      }
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
  button.btn {
    margin-right: 5px;
  }
  }
</style>
